"use client";

import { useState } from "react";
import Link from "next/link";
import { useParams } from "next/navigation";
import { motion, AnimatePresence } from "framer-motion";
import {
    ArrowLeft,
    UserCheck,
    AlertTriangle,
    Star,
    IndianRupee,
    FileText,
    Utensils
} from "lucide-react";

export default function RoomDetailPage() {
    const params = useParams();
    const roomId = params.roomId;
    const [activeTab, setActiveTab] = useState("Guest Checkin");

    const sidebarItems = [
        { name: "Guest Checkin", icon: UserCheck },
        { name: "Total Complaints", icon: AlertTriangle },
        { name: "Star Rating", icon: Star },
        { name: "Total Revenue", icon: IndianRupee },
        { name: "Transaction", icon: FileText },
        { name: "Restaurant", icon: Utensils },
    ];

    return (
        <div className="flex flex-col h-[calc(100vh-100px)]">

            {/* Header */}
            <div className="flex items-center justify-between mb-6">
                <div className="flex items-center gap-4">
                    <h1 className="text-2xl font-bold text-slate-800">
                        All Transactions - Room {roomId}
                    </h1>
                    <span className="px-3 py-1 bg-red-100 text-red-700 text-xs font-bold uppercase rounded-full">
                        Occupied
                    </span>
                </div>
                <Link
                    href="/dashboard/records"
                    className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg text-sm font-medium flex items-center gap-2 transition-colors shadow-lg shadow-blue-500/30"
                >
                    <ArrowLeft className="w-4 h-4" />
                    Back to Transactions
                </Link>
            </div>

            {/* Content Layout */}
            <div className="flex flex-1 gap-6 overflow-hidden">

                {/* Sidebar */}
                <div className="w-64 flex-shrink-0 bg-white rounded-2xl shadow-sm border border-slate-100 overflow-hidden h-full flex flex-col">
                    <div className="p-4 border-b border-slate-50">
                        <p className="text-xs font-bold text-slate-400 uppercase tracking-wider">Menu</p>
                    </div>
                    <div className="flex-1 overflow-y-auto p-2 space-y-1">
                        {sidebarItems.map((item) => {
                            const isActive = activeTab === item.name;
                            return (
                                <button
                                    key={item.name}
                                    onClick={() => setActiveTab(item.name)}
                                    className={`w-full flex items-center gap-3 px-4 py-3 rounded-xl text-sm font-medium transition-all duration-200 ${isActive
                                            ? "bg-blue-600 text-white shadow-md shadow-blue-500/30"
                                            : "text-slate-600 hover:bg-slate-50 hover:text-slate-900"
                                        }`}
                                >
                                    <item.icon className={`w-5 h-5 ${isActive ? "text-white" : "text-slate-400"}`} />
                                    {item.name}
                                </button>
                            )
                        })}
                    </div>
                </div>

                {/* Main Panel */}
                <div className="flex-1 bg-white rounded-2xl shadow-sm border border-slate-100 p-8 h-full overflow-y-auto relative">
                    <AnimatePresence mode="wait">
                        <motion.div
                            key={activeTab}
                            initial={{ opacity: 0, x: 10 }}
                            animate={{ opacity: 1, x: 0 }}
                            exit={{ opacity: 0, x: -10 }}
                            transition={{ duration: 0.2 }}
                            className="h-full"
                        >
                            <div className="flex flex-col items-center justify-center h-full text-slate-400">
                                <div className="w-16 h-16 bg-slate-50 rounded-full flex items-center justify-center mb-4">
                                    {sidebarItems.find(i => i.name === activeTab)?.icon({ className: "w-8 h-8 opacity-50" }) as any}
                                </div>
                                <h3 className="text-lg font-semibold text-slate-600">
                                    {activeTab} Panel
                                </h3>
                                <p className="text-sm">Content for {activeTab} will appear here.</p>
                            </div>
                        </motion.div>
                    </AnimatePresence>
                </div>

            </div>
        </div>
    );
}
